/**
 * Here we display the entire parsed, finsemble configuration.
 */
import React, { Component } from "react";
import LoggerStore from "../../../stores/LoggerStore";
import ConfigSearchBox from "./ConfigSearchBox";
import Highlighter from "../../highlighter/Highlighter";
class ConfigViewer extends Component {
	constructor() {
		super();
		this.state = {
			config: [],
			showSearchBox: LoggerStore.getShowSearchBox(),
			searchBoxText: LoggerStore.getSearchBoxString()
		};
		this.setShowSearchBox = this.setShowSearchBox.bind(this);
		this.onSearchUpdate = this.onSearchUpdate.bind(this);
		this.scrollToActiveLine = this.scrollToActiveLine.bind(this);

	}

	setShowSearchBox() {
		this.setState({
			showSearchBox: LoggerStore.getShowSearchBox()
		});
	}

	onSearchUpdate() {
		this.setState({
			searchBoxText: LoggerStore.getSearchBoxString()
		});
	}

	componentWillUnmount() {
		LoggerStore.removeListener("showSearchBoxChange", this.setShowSearchBox);
		LoggerStore.removeListener("searchBoxTextChange", this.onSearchUpdate);
		LoggerStore.removeListener("activeConfigSearchMatch", this.scrollToActiveLine);
	}

	componentWillMount() {
		LoggerStore.addListener("searchBoxTextChange", this.onSearchUpdate);
		LoggerStore.addListener("showSearchBoxChange", this.setShowSearchBox);
		LoggerStore.addListener("activeConfigSearchMatch", this.scrollToActiveLine);
		LoggerStore.getFullConfig((cfg) => {
			this.setState({ config: cfg });
		});
	}

	scrollToActiveLine(err, value) {
		let activeLine = LoggerStore.getActiveConfigSearchMatch();
		let activeLineDOMReference = this.refs[`line${activeLine}`];
		if (activeLineDOMReference) {
			activeLineDOMReference.scrollIntoView();
		}
	}

	render() {
		let cfg = this.state.config;
		return (
			<div className="config-viewer">
				<div className="config">
					{cfg.map((line, i) => {
						let str = `line${i}`;
						return (<pre ref={str} key={i}>
							<Highlighter
								ind={i}
								str={line}
								searchBoxText={this.state.searchBoxText}
							/>
						</pre>);
					})}
				</div>
				<ConfigSearchBox show={this.state.showSearchBox} />
			</div>
		);
	}
}

export default ConfigViewer;


