import React from "react";
import PropTypes from "prop-types";
import ObjectName from "../object/ObjectName";
import ObjectValue from "../object/ObjectValue";

/**
 * if isNonenumerable is specified, render the name dimmed
 */
const ObjectLabel = ({ onMatchFound, searchBoxText, filterStrings, name, data, isNonenumerable }) => {
	const object = data;

	return (
    <span>
      <ObjectName
        onMatchFound={onMatchFound}
        searchBoxText={searchBoxText}
        filterStrings={filterStrings}
        name={name} dimmed={isNonenumerable} />
      <span>: </span>
      <ObjectValue
        onMatchFound={onMatchFound}
        searchBoxText={searchBoxText}
        filterStrings={filterStrings} object={object} />
    </span>
	);
};

ObjectLabel.propTypes = {
  /** Non enumerable object property will be dimmed */
	isNonenumerable: PropTypes.bool,
};

ObjectLabel.defaultProps = {
	isNonenumerable: false,
};

export default ObjectLabel;



