/**
 * The main UI file for this program.
 */
import React from "react";
import LoggerStore from "./stores/LoggerStore";
import TopCollar from "./components/TopCollar";
import LogView from "./components/Views/Logs/LogView";
import ConfigViewer from "./components/Views/Config/ConfigViewer";
import "./app.css";
import _debounce from "lodash.debounce";
import ViewSwitcher from "./components/ViewSwitcher";
const CLIENTLIST_HEIGHT_OFFSET = 0;
export default class App extends React.Component {
	constructor(props) {
		super(props);

		var logState = LoggerStore.getLogState();
		let clientListVisible = LoggerStore.getClientListVisible();
		this.state = {
			activeView: LoggerStore.getActiveView(),
			clientListWidth: clientListVisible ? 500 : 0,
			clientListHeight: window.outerHeight - CLIENTLIST_HEIGHT_OFFSET,
			//There's a mysterious 16px I can't account for.
			logWidth: clientListVisible ? window.outerWidth - 500 : window.outerWidth - 16,
			fileMode: LoggerStore.getFileMode(),
			clientListVisible: clientListVisible,
			windowVisibility: LoggerStore.getWindowVisibility(),
			showSearchBox: LoggerStore.getShowSearchBox()
		};
		this.fileMode = this.fileMode.bind(this);
		this.updateClientListVisible = this.updateClientListVisible.bind(this);
		this.updateWindowVisibility = this.updateWindowVisibility.bind(this);
		this.onkeydown = this.onkeydown.bind(this);
		this.getClientListClasses = this.getClientListClasses.bind(this);
		this.onResizeWindow = this.onResizeWindow.bind(this);
		this.setContainerWidths = _debounce(this.setContainerWidths.bind(this), CLIENTLIST_HEIGHT_OFFSET);
		this.setActiveView = this.setActiveView.bind(this);
		this.setShowSearchBox = this.setShowSearchBox.bind(this);


	}

	componentWillMount() {
		LoggerStore.addListener("FileMode", this.fileMode);
		LoggerStore.addListener("clientListVisibleChange", this.updateClientListVisible);
		LoggerStore.addListener("WindowVisibilityChange", this.updateWindowVisibility);
		LoggerStore.addListener("viewChange", this.setActiveView);
		LoggerStore.addListener("showSearchBoxChange", this.setShowSearchBox);
		window.addEventListener("keydown", this.onkeydown);
		window.addEventListener("resize", this.onResizeWindow);
	}

	componentWillUnmount() {
		LoggerStore.removeListener("FileMode", this.fileMode);
		LoggerStore.removeListener("clientListVisibleChange", this.updateClientListVisible);
		LoggerStore.removeListener("WindowVisibilityChange", this.updateWindowVisibility);
		LoggerStore.removeListener("viewChange", this.setActiveView);
		LoggerStore.removeListener("showSearchBoxChange", this.setShowSearchBox);
		window.removeEventListener("resize", this.onResizeWindow);
		window.removeEventListener("keydown", this.onkeydown);
	}
	setShowSearchBox() {
		this.setState({
			showSearchBox: LoggerStore.getShowSearchBox()
		});
	}
	onkeydown(e) {
		if (e.ctrlKey) {
			if (e.key === "ArrowUp") {
				LoggerStore.setTop();
			} else if (e.key === "ArrowDown") {
				LoggerStore.setBottom();
			} else if (e.key === "ArrowLeft" || e.key === "ArrowRight") {
				LoggerStore.scrollToActiveRow();
			} else if (e.key === "f") {
				LoggerStore.showSearchBox();
			}
			return;
		}

		if (this.state.showSearchBox && e.key === "Escape") {
			LoggerStore.hideSearchBox();
		}
	}
	setActiveView() {
		let activeView = LoggerStore.getActiveView();
		//prevent unnecessary renders...
		if (activeView === this.state.activeView) return;
		this.setState({
			activeView: activeView
		});
	}

	fileMode() {
		var fileModeState = LoggerStore.getFileMode();
		this.setState({ fileMode: fileModeState });
	}
	updateClientListVisible() {
		let clientListVisible = LoggerStore.getClientListVisible();
		this.setState({
			clientListVisible: clientListVisible,
			logWidth: clientListVisible ? window.outerWidth - 500 : window.outerWidth - 16,
			clientListWidth: clientListVisible ? 500 : 0,
		}, LoggerStore.triggerLogRerender.bind(LoggerStore));
	}
	updateWindowVisibility() {
		this.setState({
			windowVisibility: LoggerStore.getWindowVisibility()
		});
	}
	setContainerWidths(e) {
		//coming from the resize-grid. If not, coming from window.
		let { logWidth, clientListWidth, clientListHeight } = this.state;
		if (e.resizeChilds) {
			logWidth = e.resizeChilds[1].width;
			clientListWidth = e.resizeChilds[0].width;
		}
		clientListHeight = window.outerHeight - CLIENTLIST_HEIGHT_OFFSET;
		this.setState({ clientListWidth, logWidth, clientListHeight });
	}
	getClientListClasses() {
		if (this.state.clientListWidth < 500) {
			return "left-panel client-list-small";
		}
		return "left-panel";
	}

	onResizeWindow(e) {
		LoggerStore.triggerRowheightRecalculation();
		this.setContainerWidths(e);
	}
	// currently plain-text mode is never used, but hook left in case needed in future
	render() {
		if (this.state.windowVisibility === "hidden") return null;
		return (
			<div className="body-wrapper">
				<TopCollar activeView={this.state.activeView} />
				<div className="inner-content-wrapper">
					<ViewSwitcher activeView={this.state.activeView} />
					{this.state.activeView === "logger" &&
						<LogView clientListVisible={this.state.clientListVisible} />
					}
					{this.state.activeView === "config" &&
						<ConfigViewer />
					}
				</div>
			</div >);
	}
}



