/**
 * Handles string-based, and level-based filtering.
 */
export default {
	deriveMethods(context) {
		let methods = Object.keys(this);
		//Takes each method on this object and binds the incoming context to that method.
		methods.forEach(method => {
			context[method] = this[method].bind(context);
		});
	},

	setFilter1(searchString) {
		this.persistState.set("highlightString[0]", {
			str: searchString.toString()
		});
		if (this.persistState.data.filterHighlights) {
			this.filterData();
		}
		this.emit("NewHighlightValues1");
	},


	setFilter2(searchString) {
		this.persistState.set("highlightString[1]", {
			str: searchString.toString()
		});
		if (this.persistState.data.filterHighlights) {
			this.filterData();
		}
		this.emit("NewHighlightValues2");
	},

	setFilter3(searchString) {
		this.persistState.set("highlightString[2]", {
			str: searchString.toString()
		});
		if (this.persistState.data.filterHighlights) {
			this.filterData();
		}
		this.emit("NewHighlightValues3");
	},

	setFilter4(searchString) {
		this.persistState.set("highlightString[3]", {
			str: searchString.toString()
		});
		if (this.persistState.data.filterHighlights) {
			this.filterData();
		}
		this.emit("NewHighlightValues4");
	},
	getFilterStrings(params = { removeEmpty: false }) {
		let strings = this.persistState.get("highlightString");
		if (!strings) strings = [];
		strings = strings.map(filter => {
			if (typeof filter === "string") filter = { str: filter };
			return filter;
		});
		if (params.removeEmpty) {
			return strings.filter(filter => {
				let str = filter.str;
				return str !== "";
			});
		}
		return strings;
	},
	//Toggles  all of the log-levels
	toggleDisplayAll() {
		var logState = this.persistState.data.logState;
		// if any not true, then set them all to true; otherwise make them all false
		if (logState.Error && logState.Warn && logState.Info && logState.Log && logState.Debug && logState.Verbose) {
			this.persistState.set("logState", { Error: false, Warn: false, Info: false, Log: false, Debug: false, Verbose: false });
		} else {
			this.persistState.set("logState", { Error: true, Warn: true, Info: true, Log: true, Debug: true, Verbose: true });
		}
		this.filterData();
		this.emit("newDisplayState");
	},

	toggleDisplayErrors() {
		this.persistState.toggle("logState.Error");
		this.filterData();
		this.emit("newDisplayState");
	},

	toggleDisplayWarnings() {
		this.persistState.toggle("logState.Warn");
		this.filterData();
		this.emit("newDisplayState");
	},

	toggleDisplayInfo(alue) {
		this.persistState.toggle("logState.Info");
		this.filterData();
		this.emit("newDisplayState");
	},

	toggleDisplayLogs() {
		this.persistState.toggle("logState.Log");
		this.filterData();
		this.emit("newDisplayState");
	},

	toggleDisplayDebug() {
		this.persistState.toggle("logState.Debug");
		this.filterData();
		this.emit("newDisplayState");
	},

	toggleDisplayVerbose() {
		this.persistState.toggle("logState.Verbose");
		this.filterData();
		this.emit("newDisplayState");
	},

	getDevModeState() {
		return this.persistState.get("devModeState");
	},

	getSystemModeState() {
		return this.persistState.get("systemModeState");
	},

	getPerfModeState() {
		return this.persistState.get("perfModeState");
	},

	devModeToggle() {
		this.persistState.toggle("devModeState");
		this.filterData();
		this.emit("ModeStateChange");
	},

	systemModeToggle() {
		this.persistState.toggle("systemModeState");
		this.filterData();

		this.emit("ModeStateChange");
	},

	perfModeToggle() {
		this.persistState.toggle("perfModeState");
		this.filterData();
		this.emit("ModeStateChange");
	},

	devMode() {
		this.persistState.set("currentCategory", "dev");
		this.emit("ChangeClientMode");
		this.emit("newClientState");
	},

	systemMode() {
		this.persistState.set("currentCategory", "system");
		this.emit("ChangeClientMode");
		this.emit("newClientState");
	},

	perfMode() {
		this.persistState.set("currentCategory", "perf");
		this.emit("ChangeClientMode");
		this.emit("newClientState");
	},

	//Filtering
	getFilterLogic() {
		return this.persistState.get("filter.logic");
	},
	setFilterLogic(logic) {
		this.persistState.set("filter.logic", logic);
		this.emit("filterLogicChange");
		this.filterData();

	},

	shouldFilterData() {
		let searchStrings = this.getFilterStrings({ removeEmpty: true });
		let allFiltersEmpty = searchStrings.length === 0;
		//if ever log level is true, return true. This is almost never true.
		let logCategoriesAllowed = this.getLogCategoriesAllowed();

		let logLevelsAllowed = this.getLogState();
		let allClientVisibility = this.getAllShowClientState();
		let allLogLevelsAllowed = Object.keys(logLevelsAllowed).every(level => logLevelsAllowed[level]);
		let allLogCategoriesAllowed = Object.keys(logCategoriesAllowed).every(category => logCategoriesAllowed[category]);
		let allClientsAreVisible = Object.keys(allClientVisibility).every(client => allClientVisibility[client]);
		//If everything is allowed, we should not filter data. If any are false (that is, some filter is enabled), we should filter data.
		return !(allLogCategoriesAllowed && allClientsAreVisible && allFiltersEmpty && allLogLevelsAllowed);
	},
	shouldNotFilterData() {
		return !this.shouldFilterData();
	},
	/**
	 * Main file
	 * @param {LogMessage} logMessage
	 */
	filterData(logMessage) {
		let filterAll = typeof (logMessage) === "undefined";
		let masterLog = this.getMasterLog();
		let filteredLog = this.getFilteredLog();
		let unfilteredData = filterAll ? masterLog : [logMessage];
		let searchStrings = this.getFilterStrings({ removeEmpty: true });
		let allFiltersEmpty = searchStrings.length === 0;
		let allClientVisibility = this.getAllShowClientState();

		//if ever log level is true, return true. This is almost never true.
		let logCategoriesAllowed = this.getLogCategoriesAllowed();
		let logLevelsAllowed = this.getLogState();
		let shouldNotFilterData = this.shouldNotFilterData();
		if (shouldNotFilterData) {
			this.setFilteredLog(masterLog);
			if (filterAll) {
				this.broadCastNewMessageUpdate();
			}
			return filteredLog;
		}

		let filterLogic = this.getFilterLogic();

		function filterFunction(logEntry, searchString) {
			if (!allClientVisibility[logEntry.logClientName]) return false;

			let logLevelAllowed = logLevelsAllowed[logEntry.logType];
			let logCategoryAllowed = logCategoriesAllowed[logEntry.category];
			let result = allFiltersEmpty;
			//No search string, all that matters is that the log level and category is a-okay.
			if (searchString === null) {
				result = true;
			} else if (logCategoryAllowed && logLevelAllowed && !allFiltersEmpty) {
				if (searchString && searchString.length > 0) {
					if (searchString === "-") return true;
					let str = searchString;
					let argMatchesFilter = logEntry.allLogArgs.toLowerCase().indexOf(str.toLowerCase()) !== -1;
					//If we're trying to exclude messages based on a string, and the log has that string, it should not be included in the final output. If it doesn't have the string in its log, it should be included in the final output.
					result = result || argMatchesFilter;
				}
				//If allFilters are empty, we return true.
			} else if (filterLogic === "AND") {
				result = true;
			}
			return logCategoryAllowed && logLevelAllowed && result;
		}

		let filteredData = unfilteredData.filter((el, i) => {
			if (allFiltersEmpty) {
				return filterFunction(unfilteredData[i], null);
			}
			if (filterLogic === "AND") {
				//if one of the search strings is _not_ in the log, it fails.
				for (let s = 0; s < searchStrings.length; s++) {
					let excluding = false;
					let str = searchStrings[s].str;
					if (str.charAt(0) === "-") {
						str = str.substr(1);
						excluding = true;
					}

					let matchesFilter = filterFunction(unfilteredData[i], str);
					if (!matchesFilter && !excluding) {
						return false;
					} else if (excluding) {
						//if the filter matches and we're excluding, return false. Otherwise wait for other, non-exclusionary matches to break the loop.
						if (matchesFilter) {
							return false;
						}
					}
				}
				return true;
			}

			///OR CASE BELOW
			let matchFound = false;
			//if one of the search strings are in the log, and it wasn't excluded by another search, it passes.
			for (let s = 0; s < searchStrings.length; s++) {
				let str = searchStrings[s].str;
				let excluding = false;
				if (str.charAt(0) === "-") {
					str = str.substr(1);
					excluding = true;
				}

				let matchesFilter = filterFunction(unfilteredData[i], str);
				//if any search string matches, it's included in the result.
				if (matchesFilter) {
					//If this string matches and we're excluding this string, kick out the log.
					if (excluding) {
						return false;
					}
					matchFound = true;
				}
			}

			return matchFound;

		});

		if (filterAll) {
			//'this' is the Store.
			this.setFilteredLog(filteredData);
		}

		if (filterAll) {
			this.broadCastNewMessageUpdate();
		}

		return filteredData;
	}
};


