/**
 * Collar at the top of the page housing import/export, localhost:9090 link, and a link to the process monitor.
 */
import React from "react";
import LoggerStore from "../stores/LoggerStore";
import LoggerButton from "./LoggerButton";
import { System } from "../../../../common/system";

import LauncherClient from "../../../../clients/launcherClient";
LauncherClient.initialize();

// high-level affects on central logger.  Buttons to invoke ClearData, Export, Import, and Save Settings.
export default class TopCollar extends React.PureComponent {
	constructor(props) {
		super(props);
		this.state = {
			logCount: LoggerStore.getLogCounts(),
			clientListVisible: LoggerStore.getClientListVisible(),
			showProcessMonitor: false
		};

		this.getNewLogCounts = this.getNewLogCounts.bind(this);
		this.updateClientListVisible = this.updateClientListVisible.bind(this);
		this.calculateMemoryUsage = this.calculateMemoryUsage.bind(this);
		this.getShowProcessMonitor = this.getShowProcessMonitor.bind(this);
		this.getShowProcessMonitor();
	}

	componentWillMount() {
		LoggerStore.addListener("loggerNewMessages", this.getNewLogCounts);
		LoggerStore.addListener("clientListVisibleChange", this.updateClientListVisible);

	}
	getShowProcessMonitor() {
		//Wait for the launcher to come up, then figure out whether we should show the button to spawn the process monitor.
		LauncherClient.onReady(() => {
			LauncherClient.getComponentList((err, componentList) => {
				this.setState({
					showProcessMonitor: componentList && "Process Monitor" in componentList
				});
			});
		});
	}
	componentWillUnmount() {
		LoggerStore.removeListener("loggerNewMessages", this.getNewLogCounts);
		LoggerStore.removeListener("clientListVisibleChange", this.updateClientListVisible);
	}

	clearData() {
		LoggerStore.clearData();
	}

	saveToFile() {
		LoggerStore.saveToFile();
	}

	fileMode() {
		LoggerStore.fileMode();
	}

	persist() {
		LoggerStore.persist();
	}

	getNewLogCounts() {
		var counts = LoggerStore.getLogCounts();
		this.setState({ logCount: counts });
	}

	toggleClientList() {
		LoggerStore.toggleClientListVisible();
	}
	updateClientListVisible() {
		this.setState({
			clientListVisible: LoggerStore.getClientListVisible()
		});
	}
	spawnProcessMonitor() {
		LauncherClient.spawn("Process Monitor");
	}
	spawnLocalhost9090() {
		System.openUrlWithBrowser("http://localhost:9090");
	}

	calculateMemoryUsage() {
		let messageList = LoggerStore.getCurrentLog();
		function roughSizeOfObject(object) {
			var objectList = [];
			var stack = [object];
			var bytes = 0;
			var attempts = 0;
			while (stack.length && attempts < 100) {
				attempts++;
				var value = stack.pop();

				if (typeof value === "boolean") {
					bytes += 4;
				}
				else if (typeof value === "string") {
					bytes += value.length * 2;
				}
				else if (typeof value === "number") {
					bytes += 8;
				}
				else if
				(
					typeof value === "object"
					&& objectList.indexOf(value) === -1
				) {
					objectList.push(value);

					for (var i in value) {
						stack.push(value[i]);
					}
				}
			}
			return bytes;
		}
		let memoryUsage = {};
		for (var i = 0; i < messageList.length; i++) {
			let log = messageList[i];
			if (typeof memoryUsage[log.logClientName] === "undefined") {
				memoryUsage[log.logClientName] = 0;
			}
			memoryUsage[log.logClientName] += roughSizeOfObject(log.allLogArgs);
		}
		this.setState({
			memoryUsage: memoryUsage
		});
	}
	bytesToSize(bytes) {
		var sizes = ["Bytes", "KB", "MB", "GB", "TB"];
		if (bytes == 0) return "0 Byte";
		var i = parseInt(Math.floor(Math.log(bytes) / Math.log(1024)));
		return Math.round(bytes / Math.pow(1024, i), 2) + " " + sizes[i];
	}

	render() {
		let memoryUsage = {};
		for (var client in this.state.memoryUsage) {
			memoryUsage[client] = this.bytesToSize(this.state.memoryUsage[client]);
		}
		window.LoggerMemoryUsage = memoryUsage;

		return (<div className="logger-row top-collar row-shadow">
			{this.props.activeView === "logger" &&
				<LoggerButton
					title="CTRL+ALT+C"
					onClick={this.toggleClientList}>
					{this.state.clientListVisible ? "Hide " : "Show "}
					Left Panel</LoggerButton>
			}
			{this.state.showProcessMonitor && <LoggerButton
				onClick={this.spawnProcessMonitor}>
				Process Monitor</LoggerButton>
			}
			<LoggerButton
				onClick={this.spawnLocalhost9090}>
				localhost:9090</LoggerButton>
			<div className="pull-right">
				<div className="fsbl-logger-buttons" >
					<LoggerButton onClick={this.saveToFile}>Export Log</LoggerButton>
					<LoggerButton onClick={this.fileMode}>Import Log</LoggerButton>
					{/* <LoggerButton onClick={this.persist}>Save Settings</LoggerButton> */}
				</div>
			</div>
		</div >);

	}
}



