/**
 * The finsemble console. It displays the filtered logs
 */

import React from "react";
import LoggerStore from "../../../../stores/LoggerStore";
import AutoSizer from "react-virtualized/dist/commonjs/AutoSizer";
import ObjectInspector from "../../../objectInspector/object-inspector/ObjectInspector";
import inspectorTheme from "../objectInspectorTheme";
import { CellMeasurer, CellMeasurerCache, List } from "react-virtualized";
import { IconMap } from "./chromeStyleIcons";
import Highlighter from "../../../highlighter/Highlighter";
Object.defineProperty(Date.prototype, "YYYYMMDDHHMMSS", {
	value: function () {
		function pad2(n) {  // always returns a string
			return (n < 10 ? "0" : "") + n;
		}
		function pad3(n) {  // always returns a string
			if (n < 10) {
				return `${n}00`;
			}
			if (n < 100) {
				return `${n}0`;
			}
			return n;
		}
		return pad2(this.getHours()) + ":" +
			pad2(this.getMinutes()) + ":" +
			pad2(this.getSeconds()) + ":" +
			pad3(this.getMilliseconds());
	}
});
//@todo, investigate why this is necessary...think I included it for chrome styles. Didn't end up using chrome styles.
require("./consoleView.css");
const baseLogClass = "fsbl-log-message fsbl-plaintext-message-wrapper console-message-wrapper";
const logTypeClasses = {
	"Verbose": `${baseLogClass} fsbl-log-verbose`,
	"Log": `${baseLogClass} console-log-level`,
	"Info": `${baseLogClass} console-info-level`,
	"Debug": `${baseLogClass} console-debug-level`,
	"Warn": `${baseLogClass} console-warning-level`,
	"Error": `${baseLogClass} console-error-level`
};

const cache = new CellMeasurerCache({
	defaultHeight: 50,
	fixedWidth: true
});

var ListRef;
function setRef(ref) {
	ListRef = ref;
	window.ListRef = ListRef;
}
// rought cut on component for raw table output.  Currently not used but may need to enhance if "View JSON" is too slow or limited
export default class LogList extends React.PureComponent {
	constructor(props) {
		super(props);
		this.update = false;
		this.ctrl = false;
		this.filteredData = LoggerStore.getFilteredLog();
		this.state = {
			tableWidth: 100,
			tableHeight: 100,
			logCounts: LoggerStore.getLogCounts(),
			searchBoxIsVisible: LoggerStore.getShowSearchBox(),
			searchBoxText: LoggerStore.getSearchBoxString(),
			filterStrings: this.getFilterStrings(true),
			allowedLogLevels: LoggerStore.getLogState(),
			allClientVisibility: LoggerStore.getAllShowClientState(),
			firstMessageTime: LoggerStore.getFirstMessageTime(),
			//These two are used to keep track of your position as you're filtering.
			activeRowTimestamp: 0,
			activeRowIndex: null,
			clientListVisible: LoggerStore.getClientListVisible(),
			comparisonRows: [],
		};
		this.loggerGetNewMessages = this.loggerGetNewMessages.bind(this);
		this.windowResize = this.windowResize.bind(this);
		this.rowRenderer = this.rowRenderer.bind(this);
		this.onRowClicked = this.onRowClicked.bind(this);
		this.setSearchBoxIsVisible = this.setSearchBoxIsVisible.bind(this);
		this.updateSearchBoxText = this.updateSearchBoxText.bind(this);
		this.getFilterStrings = this.getFilterStrings.bind(this);
		this.setFirstMessageTime = this.setFirstMessageTime.bind(this);
		this.setBottom = this.setBottom.bind(this);
		this.setActiveRowTimestamp = this.setActiveRowTimestamp.bind(this);
		this.scrollToActiveRow = this.scrollToActiveRow.bind(this);
		this.addToRowDiff = this.addToRowDiff.bind(this);
		this.onKeyDown = this.onKeyDown.bind(this);
		this.onKeyUp = this.onKeyUp.bind(this);
		this.onActiveRowChanged = this.onActiveRowChanged.bind(this);
		window.consoleComponent = this;
	}

	componentWillMount() {
		LoggerStore.addListener("loggerNewMessages", this.loggerGetNewMessages);
		LoggerStore.addListener("newShowClientState", this.loggerGetNewMessages);
		LoggerStore.addListener("showSearchBoxChange", this.setSearchBoxIsVisible);
		LoggerStore.addListener("searchBoxTextChange", this.updateSearchBoxText);
		LoggerStore.addListener("activeRowChanged", this.onActiveRowChanged);
		LoggerStore.addListener("NewHighlightValues1", this.getFilterStrings);
		LoggerStore.addListener("NewHighlightValues2", this.getFilterStrings);
		LoggerStore.addListener("NewHighlightValues3", this.getFilterStrings);
		LoggerStore.addListener("NewHighlightValues4", this.getFilterStrings);
		LoggerStore.addListener("rerenderList", this.rerenderList);
		LoggerStore.addListener("recalculateHeight", this.recalculateAllRows);
		LoggerStore.addListener("newFirstMessageTime", this.setFirstMessageTime);
		LoggerStore.addListener("SetTop", this.setTop);
		LoggerStore.addListener("SetBottom", this.setBottom);
		LoggerStore.addListener("scrollToActiveRow", this.scrollToActiveRow);
		window.addEventListener("keydown", this.onKeyDown);
		window.addEventListener("keyup", this.onKeyUp);

	}

	componentWillUnmount() {
		LoggerStore.removeListener("loggerNewMessages", this.loggerGetNewMessages);
		LoggerStore.removeListener("newShowClientState", this.loggerGetNewMessages);
		LoggerStore.removeListener("showSearchBoxChange", this.setSearchBoxIsVisible);
		LoggerStore.removeListener("activeRowChanged", this.onActiveRowChanged);
		LoggerStore.removeListener("searchBoxTextChange", this.updateSearchBoxText);
		LoggerStore.removeListener("NewHighlightValues1", this.getFilterStrings);
		LoggerStore.removeListener("NewHighlightValues2", this.getFilterStrings);
		LoggerStore.removeListener("NewHighlightValues3", this.getFilterStrings);
		LoggerStore.removeListener("NewHighlightValues4", this.getFilterStrings);
		LoggerStore.removeListener("rerenderList", this.rerenderList);
		LoggerStore.removeListener("recalculateHeight", this.recalculateAllRows);
		LoggerStore.removeListener("newFirstMessageTime", this.setFirstMessageTime);
		LoggerStore.removeListener("SetTop", this.setTop);
		LoggerStore.removeListener("SetBottom", this.setBottom);
		LoggerStore.removeListener("scrollToActiveRow", this.scrollToActiveRow);
		window.removeEventListener("keydown", this.onKeyDown);
		window.removeEventListener("keyup", this.onKeyUp);
	}

	setFirstMessageTime() {
		this.setState({
			firstMessageTime: LoggerStore.getFirstMessageTime()
		});
	}
	//whether to return or set state...the constructor needs the values. everywhere else is setState
	getFilterStrings(returnStrings = false) {
		let filterStrings = LoggerStore.getFilterStrings();
		filterStrings = filterStrings.map(filter => {
			return filter.str.split(" ");
		});
		if (returnStrings) return filterStrings;
		this.setState({
			filterStrings
		});

	}
	updateSearchBoxText() {
		this.setState({
			searchBoxText: LoggerStore.getSearchBoxString()
		});
	}

	setSearchBoxIsVisible() {
		this.setState({
			searchBoxIsVisible: LoggerStore.getShowSearchBox()
		});
	}

	setActiveRowTimestamp(timestamp) {
		this.setState({
			activeRowTimestamp: timestamp,
			activeRowIndex: this.getActiveRowIndex(timestamp)
		});
	}

	windowResize() {
		var newTableWidth = LoggerStore.getWindowWidth();
		let windowHeight = LoggerStore.getWindowHeight();
		let logHeader = document.querySelector("#message-list");
		let offsetTop = logHeader ? logHeader.getBoundingClientRect().top : 600;
		var newTableHeight = Math.abs(windowHeight - offsetTop - 5);
		this.update = true;
		this.setState({
			tableWidth: newTableWidth,
			tableHeight: newTableHeight
		});
	}

	loggerGetNewMessages() {
		this.update = true;
		let log = LoggerStore.getFilteredLog();
		this.filteredData = log;
		this.setState({
			activeRowIndex: this.getActiveRowIndex()
		});
	}

	typeStyle(type) {
		var style;
		if (type == "Error") {
			style = { color: "red" };
		} else if (type == "Warn") {
			style = { color: "#D4A017" };
		} else if (type == "Debug") {
			style = { color: "blue" };
		} else if (type == "Verbose") {
			style = { color: "darkblue" };
		} else {
			style = { color: "black" };
		}
		return style;
	}
	onActiveRowChanged() {
		let index = LoggerStore.getActiveRowIndex();
		this.setState({
			comparisonRows: [],
			activeRowIndex: index
		}, () => {
			this.scrollToActiveRow();
			if (this.filteredData[index]) {
				this.setActiveRowTimestamp(this.filteredData[index].logTimestamp);
			}
			this.addToRowDiff(index);
		});
	}

	onRowClicked(index = 0) {
		cache.clear(index);
		this.setActiveRowTimestamp(this.filteredData[index].logTimestamp);
		this.addToRowDiff(index);
		ListRef.recomputeRowHeights(index);
	}

	recalculateAllRows() {
		cache.clearAll();
		ListRef.recomputeRowHeights();
	}
	rerenderList() {
		cache.clearAll();
		ListRef.forceUpdate();
	}

	scrollToActiveRow() {
		if (this.state.activeRowIndex === null || typeof this.state.activeRowIndex === "undefined") return;
		ListRef.recomputeRowHeights(this.state.activeRowIndex);
		ListRef.scrollToRow(this.state.activeRowIndex);
	}

	setTop() {
		ListRef.scrollToRow(0);
	}

	setBottom() {
		ListRef.recomputeRowHeights(this.filteredData.length - 1);
		ListRef.scrollToRow(this.filteredData.length - 1);
	}

	openClient(logClientName) {
		LoggerStore.openClient(logClientName);
	}

	addToRowDiff(index) {
		let row = this.filteredData[index];
		let rowIndexes = this.state.comparisonRows.filter(row => row !== undefined);
		let rowDiff = null;
		if (this.state.comparisonRow) {
			if (this.ctrl) {
				if (rowIndexes.length == 1) {
					rowIndexes.push(index);
				} else {
					rowIndexes = [index];
				}
				row = this.state.comparisonRow;
			} else {
				rowIndexes = [index];
			}
		} else {
			rowIndexes = [index];
		}

		if (rowIndexes.length === 2) {
			rowDiff = Math.abs(this.filteredData[rowIndexes[0]].timeElapsedFromStartup - this.filteredData[rowIndexes[1]].timeElapsedFromStartup);
		}

		LoggerStore.setRowTimeDiff(rowDiff);
		this.update = true;
		this.setState({
			comparisonRow: row,
			comparisonRows: rowIndexes
		});
	}
	getLogContent(strToHighlight, ind) {
		let messageClasses = "highlighted-message";
		if (typeof strToHighlight === "string" && strToHighlight.includes("Stack")) {
			messageClasses += " message-log-stack";
		}
		return (<span key={ind}
			className={messageClasses}
		>
			<Highlighter
				index={ind}
				className={messageClasses}
				str={strToHighlight}
				filterStrings={this.state.filterStrings}
				searchBoxText={this.state.searchBoxText}
			/>&nbsp;</span>);
	}

	rowRenderer({ index, isScrolling, key, parent, style }) {
		let log = this.filteredData[index];
		if (!log) return null;
		log.previousRowTimeDelta = 0;
		log.timeElapsedFromStartup = Math.round(log.logTimestamp - this.state.firstMessageTime);
		if (index > 0) {
			let previousLog = this.filteredData[index - 1];
			if (previousLog) {
				log.previousRowTimeDelta = log.timeElapsedFromStartup - previousLog.timeElapsedFromStartup;
			}
		}

		// "style" is a react prop. It is const, so it cannot be modified. Since we want to change
		// the style here we'll clone it and use the clone instead.
		let styleClone = Object.assign({}, style);
		// log.logMessageWithoutStack = log.allLogArgs.replace(/Log Stack.*/g, "");
		styleClone.wordBreak = "break-all";
		styleClone.display = "flex";
		styleClone.alignItems = "center";
		styleClone.justifyContent = "space-between";
		styleClone.fontFamily = "Consolas, Lucida Console, Courier New, monospace";
		styleClone.padding = "3px";
		styleClone.width = "calc(100% - 8px)";
		let LogIcon = IconMap[log.logType];
		let wrapperClasses = logTypeClasses[log.logType];
		if (index === this.state.activeRowIndex) {
			wrapperClasses += " active-console-row";
		}
		if (this.state.comparisonRows.includes(index)) {
			wrapperClasses += " fsbl-logger-row-selected";
		}
		let content = (
			<div
				onContextMenu={() => {
					cache.clear(index);
					ListRef.recomputeRowHeights(index);
				}}
				//to handle expansion of js objects.
				onClick={() => { this.onRowClicked(index); }}
				style={styleClone}
				className={wrapperClasses}
				key={key} >
				<LogIcon />
				<div className="time-elapsed">
					{new Date(log.logTimestamp).YYYYMMDDHHMMSS()}
				</div>
				<div className="console-message-text fsbl-plaintext-log-message" style={{ width: "100%" }}>
					{log.parsedLogArgs.map((arg, i) => {
						if (typeof arg === "object") {
							return (<span key={i} style={{
								display: "inline-block !imporant",
								marginRight: "10px"
							}}>
								<ObjectInspector
									filterStrings={this.state.filterStrings || []}
									searchBoxText={this.state.searchBoxText}
									theme={inspectorTheme}
									data={arg} />
							</span>);
						}
						return this.getLogContent(arg, i);
					})}
				</div>
				<div className="fsbl-plaintext-extra-info" onClick={() => {
					this.openClient(log.logClientName);
				}}  >
					{log.logClientName}
				</div>
			</div >);
		return (
			<CellMeasurer
				cache={cache}
				columnIndex={0}
				key={key}
				parent={parent}
				rowIndex={index}
			>
				{content}
			</CellMeasurer>
		);
	}
	componentDidMount() {
		// this.windowResize();
		ListRef.recomputeRowHeights();
	}

	shouldComponentUpdate(nextProps, nextState) {
		var update = false;
		if (LoggerStore.getWindowVisibility() === "visible") {
			update = true;
		}
		return update;
	}
	getActiveRowIndex(timestamp = this.state.activeRowTimestamp) {
		for (let i = 0; i < this.filteredData.length; i++) {
			let log = this.filteredData[i];
			if (log.logTimestamp === timestamp) {
				return i;
			}
		}
		return null;
	}
	onKeyDown(e) {
		this.ctrl = e.ctrlKey;
	}
	onKeyUp() {
		this.ctrl = false;
	}

	render() {
		cache.clearAll();
		let heightOffset = document.getElementById("log-level-filters") ? document.getElementById("log-level-filters").offsetHeight : 63;
		if (this.state.searchBoxIsVisible) {
			heightOffset += 20;
		}

		return (
			<AutoSizer>
				{({ width, height }) => {
					return (<div style={{
						"width": width + "px"
					}}>
						<List
							id="log-console"
							ref={setRef}
							height={height - heightOffset}
							width={width}
							overscanRowCount={0}
							noRowsRenderer={() => null}
							rowCount={this.filteredData.length}
							deferredMeasurementCache={cache}
							rowHeight={cache.rowHeight || 20}
							rowRenderer={this.rowRenderer}
						/>
					</div>);
				}
				}
			</AutoSizer>
		);
	}
}


