/**
 * Left panel of the log-view. Houses settings, filters, client list, etc. It's a column of content, each section is housed inside of an accordion.
 */
import React, { Component } from "react";
import LoggerClientControlList from "./ClientList/LoggerClientControlList";
import LoggerDisplayControl from "./Filters/LoggerDisplayControl";
import QuickViews from "./QuickViews";

import Settings from "./Settings/Settings";
class Accordion extends Component {
	constructor() {
		super();
		this.state = {
			visible: true
		};
		this.toggleVisibility = this.toggleVisibility.bind(this);
	}
	toggleVisibility() {
		this.setState({
			visible: !this.state.visible
		});
	}
	render() {
		let classes = "left-panel-accordion ";


		if (this.props.className) {
			classes += this.props.className;
		}
		let contentClasses = "left-panel-accordion-content";
		if (this.state.visible) {
			contentClasses += " visible ";
		}
		return (
			<div className={classes}>
				<div onClick={this.toggleVisibility} className="left-panel-accordion-label-wrap">
					<span className="left-panel-accordion-visiblity-indicator">
						{this.state.visible && <span className="ff-arrow-down"> &#9660;</span>}
						{!this.state.visible && <span className="ff-arrow-up">	&#9654;</span>}
					</span>
					{this.props.label}
				</div>
				<div className={contentClasses}>
					{this.state.visible && this.props.children}
				</div>
			</div>
		);
	}
}
class TabSwitcher extends Component {
	render() {
		return (
			<div className="left-panel">
				<QuickViews />
				<Accordion label="Settings">
					<Settings />
				</Accordion>
				<Accordion label="Filters">
					<LoggerDisplayControl />
				</Accordion>
				<Accordion
					className="client-list-accordion"
					label="Components and Services">
					<LoggerClientControlList fileMode={this.props.fileMode} />
				</Accordion>
			</div >
		);
	}
}

export default TabSwitcher;


