/**
 * Toggles and distributes which log types and categories should be captured for all of the clients.
 */
export default {
	deriveMethods(context) {
		let methods = Object.keys(this);
		//Takes each method on this object and binds the incoming context to that method.
		methods.forEach(method => {
			context[method] = this[method].bind(context);
		});
	},
	showClientAllCategory(currentCategory) {
		for (let client in this.persistState.data.showClientState) {
			if (this.persistState.data.showClientState.hasOwnProperty(client) && (!this.persistState.data.hideInactiveState || this.persistState.data.showClientState[client])) {
				let clientState = this.persistState.data.clientState[client];
				let categoryState = Object.assign({}, this.defaultClientState[currentCategory]); // get a copy of the default category state
				clientState[currentCategory] = categoryState;
				this.RouterClient.transmit(clientState.clientChannel, clientState);
			}
		}
		this.emit("newClientState");
	},
	defaultShowClientAll() {
		if (this.persistState.data.applyToAllCategories) {
			this.showClientAllCategory("console");
			this.showClientAllCategory("dev");
			this.showClientAllCategory("system");
			this.showClientAllCategory("perf");
		} else {
			this.showClientAllCategory(this.persistState.data.currentCategory);
		}
		this.emit("newClientState");
	},
	// "toggle" all the values (i.e. one for each shown client) for the given category and type
	toggleLevelForAllClients(category, type) {
		var allTrue = true;
		var oneTrue = false;

		for (let client in this.persistState.data.showClientState) {
			if (this.persistState.data.showClientState.hasOwnProperty(client) && (!this.persistState.data.hideInactiveState || this.persistState.data.showClientState[client])) {
				let clientState = this.persistState.data.clientState[client];
				if (!(clientState[this.persistState.data.currentCategory][type])) {
					allTrue = false;
				}
				if ((clientState[this.persistState.data.currentCategory][type])) {
					oneTrue = true;
				}
			}
		}

		var toggledState = !(allTrue && oneTrue);

		// now gothough though all client states setting value for state type to toggledState
		for (let client in this.persistState.data.showClientState) {
			if (this.persistState.data.showClientState.hasOwnProperty(client) && (!this.persistState.data.hideInactiveState || this.persistState.data.showClientState[client])) {
				let clientState = this.persistState.data.clientState[client];
				let oldStateValue = clientState[this.persistState.data.currentCategory][type];
				clientState[this.persistState.data.currentCategory][type] = toggledState;

				// if value changed then let the client know
				if (oldStateValue !== toggledState) {
					this.persistState.set(`clientState[${client}]`, clientState);
					this.RouterClient.transmit(clientState.clientChannel, clientState);
				}
			}
		}
		this.emit("newClientState");
	},
	allCoreToggle() {
		this.allErrorsToggle();
		this.allWarningToggle();
		this.allDebugToggle();
		this.allLogsToggle();
		this.allInfoToggle();
	},
	allErrorsToggle() {
		this.toggleLevelForAllClients(this.persistState.data.currentCategory, "Error");
	},
	allWarningToggle() {
		this.toggleLevelForAllClients(this.persistState.data.currentCategory, "Warn");
	},
	allInfoToggle() {
		this.toggleLevelForAllClients(this.persistState.data.currentCategory, "Info");
	},
	allLogsToggle() {
		this.toggleLevelForAllClients(this.persistState.data.currentCategory, "Log");
	},
	allDebugToggle() {
		this.toggleLevelForAllClients(this.persistState.data.currentCategory, "Debug");
	},
	allVerboseToggle() {
		this.toggleLevelForAllClients(this.persistState.data.currentCategory, "Verbose");
	},
	allLocalConsoleToggle() {
		this.toggleLevelForAllClients(this.persistState.data.currentCategory, "LocalOnly");
	},
	toggleAllCategory(currentCategory, specifiedToggleState) {
		var toggledState;

		if (arguments.length === 2) {
			toggledState = specifiedToggleState;
		} else {
			var allTrue = true;
			var oneTrue = false;
			for (let client in this.persistState.data.showClientState) {
				if (this.persistState.data.showClientState.hasOwnProperty(client) && (!this.persistState.data.hideInactiveState || this.persistState.data.showClientState[client])) {
					let clientState = this.persistState.data.clientState[client];
					if (!(clientState[currentCategory].Error
						&& clientState[currentCategory].Warn
						&& clientState[currentCategory].Info
						&& clientState[currentCategory].Log
						&& clientState[currentCategory].Debug)) {
						allTrue = false;
					}
					if (clientState[currentCategory].Error
						|| clientState[currentCategory].Warn
						|| clientState[currentCategory].Info
						|| clientState[currentCategory].Log
						|| clientState[currentCategory].Debug) {
						oneTrue = true;
					}
				}
			}
			toggledState = !(allTrue && oneTrue);
		}

		for (let client in this.persistState.data.showClientState) {
			if (this.persistState.data.showClientState.hasOwnProperty(client) && (!this.persistState.data.hideInactiveState || this.persistState.data.showClientState[client])) {
				let clientState = this.persistState.data.clientState[client];
				if (toggledState) {
					clientState[currentCategory] =
					{
						Error: true, Warn: true, Info: true, Log: true, Debug: true,
						Verbose: clientState[currentCategory].Verbose,
						LocalOnly: clientState[currentCategory].LocalOnly
					};
				} else {
					clientState[currentCategory] =
					{
						Error: false, Warn: false, Info: false, Log: false, Debug: false,
						Verbose: clientState[currentCategory].Verbose,
						LocalOnly: clientState[currentCategory].LocalOnly
					};
				}
				this.persistState.set(`clientState[${client}]`, clientState);
				this.RouterClient.transmit(clientState.clientChannel, clientState);
			}
		}
		return toggledState;
	},
	toggleClientStateAllCategories(clientName, type) {
		this.persistState.toggle(`clientState[${clientName}].dev[${type}]`);
		this.persistState.toggle(`clientState[${clientName}].system[${type}]`);
		this.persistState.toggle(`clientState[${clientName}].perf[${type}]`);
	},
	toggleClientErrors(clientName) {
		var clientState = this.persistState.data.clientState[clientName];
		this.toggleClientStateAllCategories(clientName, "Error");
		this.RouterClient.transmit(clientState.clientChannel, clientState);
		this.emit("newClientState");
	},
	toggleClientWarnings(clientName) {
		var clientState = this.persistState.data.clientState[clientName];
		this.toggleClientStateAllCategories(clientName, "Warn");
		this.RouterClient.transmit(clientState.clientChannel, clientState);
		this.emit("newClientState");
	},
	toggleClientInfo(clientName) {
		var clientState = this.persistState.data.clientState[clientName];
		this.toggleClientStateAllCategories(clientName, "Info");
		this.RouterClient.transmit(clientState.clientChannel, clientState);
		this.emit("newClientState");
	},
	toggleClientLogs(clientName) {
		var clientState = this.persistState.data.clientState[clientName];
		this.toggleClientStateAllCategories(clientName, "Log");
		this.RouterClient.transmit(clientState.clientChannel, clientState);
		this.emit("newClientState");
	},
	toggleClientDebug(clientName) {
		var clientState = this.persistState.data.clientState[clientName];
		this.toggleClientStateAllCategories(clientName, "Debug");
		this.RouterClient.transmit(clientState.clientChannel, clientState);
		this.emit("newClientState");
	},
	toggleClientVerbose(clientName) {
		var clientState = this.persistState.data.clientState[clientName];
		this.toggleClientStateAllCategories(clientName, "Verbose");
		this.RouterClient.transmit(clientState.clientChannel, clientState);
		this.emit("newClientState");
	},
	toggleClientLocalOnly(clientName) {
		var clientState = this.persistState.data.clientState[clientName];
		this.toggleClientStateAllCategories(clientName, "LocalOnly");
		this.RouterClient.transmit(clientState.clientChannel, clientState);
		this.emit("newClientState");
	},
	toggleInitialClientErrorsDefault() {
		this.persistState.toggle("initialClientStateDefault.Error");
		this.emit("newInitialClientStateDefault");
		this.defaultShowClientAll();
	},
	toggleInitialClientWarningsDefault() {
		this.persistState.toggle("initialClientStateDefault.Warn");
		this.emit("newInitialClientStateDefault");
		this.defaultShowClientAll();
	},
	toggleInitialClientInfoDefault() {
		this.persistState.toggle("initialClientStateDefault.Info");
		this.emit("newInitialClientStateDefault");
		this.defaultShowClientAll();
	},
	toggleInitialClientLogsDefault() {
		this.persistState.toggle("initialClientStateDefault.Log");
		this.emit("newInitialClientStateDefault");
		this.defaultShowClientAll();
	},
	toggleInitialClientDebugDefault() {
		this.persistState.toggle("initialClientStateDefault.Debug");
		this.emit("newInitialClientStateDefault");
		this.defaultShowClientAll();
	},
	toggleInitialClientVerboseDefault() {
		this.persistState.toggle("initialClientStateDefault.Verbose");
		this.emit("newInitialClientStateDefault");
		this.defaultShowClientAll();
	},
	toggleInitialClientLocalOnlyDefault() {
		this.persistState.toggle("initialClientStateDefault.LocalOnly");
		this.emit("newInitialClientStateDefault");
		this.defaultShowClientAll();
	},
	getInitialClientStateDefault() {
		return this.persistState.get("initialClientStateDefault");
	}
};


