/**
 * Small object that creates the logMessage object that we use to display everything.
 */
export default class LogMessage {
	// all incoming messages from clients are processed here
	constructor(message) {
		var newMessage = message || {};
		var parsedLogData;
		newMessage.highlightFlag = [];
		newMessage.logArgs = [];
		newMessage.parsedLogArgs = [];
		newMessage.allLogArgs = "";
		newMessage.previousRowTimeDelta = 0;
		try {
			parsedLogData = JSON.parse(message.logData);
		} catch (err) {
			parsedLogData = { Note: "*** LoggingService: error parsing data (see local console warning for data) ***" };
			console.warn(parsedLogData, message.logData, err);
		}
		//we stringify all the args below. no need for multiple copies of the same thing.
		for (var property in parsedLogData) { // don't know how many arguments passed in to console output`
			if (parsedLogData.hasOwnProperty(property)) {
				newMessage.parsedLogArgs.push(parsedLogData[property]);
				let argString = JSON.stringify(parsedLogData[property]);
				newMessage.logArgs.push(argString);
			}
		}
		//Join all of the parsed args, replace any double quotes that we created with a space.
		newMessage.allLogArgs = newMessage.logArgs.join(" ").replace("", " ").replace("\" \"", " ");
		delete newMessage.logData;
		delete newMessage.logArgs;
		return newMessage;
	}
}


