/**
 * The harness for the actual console and filters.
 */
import React, { Component } from "react";
import LogLevelFilters from "./LogLevelFilters";
import LogList from "./LogList";
import LoggerSearchbox from "../LoggerSearchbox";
import LoggerStore from "../../../../stores/LoggerStore";

class RightPanel extends Component {
	constructor() {
		super();
		this.state = {
			showSearchBox: LoggerStore.getShowSearchBox()
		};
		this.setShowSearchBox = this.setShowSearchBox.bind(this);

	}
	componentWillMount() {
		LoggerStore.addListener("showSearchBoxChange", this.setShowSearchBox);

	}
	componentWillUnmount() {
		LoggerStore.removeListener("showSearchBoxChange", this.setShowSearchBox);
	}

	setShowSearchBox() {
		this.setState({
			showSearchBox: LoggerStore.getShowSearchBox()
		});
	}

	render() {
		return (
			<div className="right-panel">
				<LogLevelFilters />
				<LogList />
				<LoggerSearchbox show={this.state.showSearchBox} />
			</div>
		);
	}
}

export default RightPanel;


