/**
 * This is not quite wholly baked yet. The idea is that the LoggerService interacts with an array of LogAdapters. When a new message comes in, it's passed to each adapter, which can do what it wants with the message. In this file, we pass it on to the Actions object, which just calls methods on the LoggerStore.
 * It's easy to conceive that instead of passing the mssage to the UI, we'd run some filter on the message and decide whether to pass it off to a server.
 */
import Actions from "./stores/Actions";
export default class LogAdapter {
	constructor() {
		this.name = "loggerUI";
		try {//webpack was crying here. It wouldnt let it build without this. Window does not exist server side.
			if (global.adapterReceiver) {
				global.adapterReceiver.loaded(this.name, this);
			} else {
				console.error("Global Adapter Reciever not found on the global object. Fatal error.");
			}
		} catch (e) {
			console.error("BaseStorage constructor", e);
		}
	}
	receiveLoggerServiceState(state) {
		Actions.setLoggerServiceState(state);
	}
	onNewMessage(message) {
		Actions.onNewMessage(message);
	}
	onClientRegistered(data) {
		return Actions.onClientRegistered(data);
	}
	onClientUnregistered(data) {
		Actions.onClientUnregistered(data);
	}
}


