import React from 'react';
import PropTypes from 'prop-types';
import TreeNode from './TreeNode';

import { DEFAULT_ROOT_PATH, hasChildNodes, getExpandedPaths } from './pathUtils';
const reducer = (state, action) => {
  switch (action.type) {
    case 'TOGGLE_EXPAND': {
      let path = action.path;
      if (!Array.isArray(path)) {
        path = [path];
      }
      let ret = Object.assign({}, state);

      path.forEach((path) => {
        const expandedPaths = state.expandedPaths;
        const expanded = !!expandedPaths[path];
        ret.expandedPaths[path] = !expanded;
      });
      return ret;
    }
    default:
      return state;
  }
};

class ConnectedTreeNode extends React.PureComponent {
  constructor(props, context) {
    super(props);
    this.state = context.store.storeState;
    this.state.depth = props.depth;
  }

  shouldComponentUpdate(nextProps, nextState) {
    const filterChanged = this.props.filterStrings.every(str => nextProps.filterStrings.includes(str));
    const searchBoxTextChanged = nextProps.searchBoxText !== this.props.searchBoxText;
    return (
      filterChanged || searchBoxTextChanged ||
      !!nextState.expandedPaths[nextProps.path] !== !!this.state.expandedPaths[this.props.path] ||
      nextProps.data !== this.props.data ||
      nextProps.name !== this.props.name
    );
  }
  //Figure out later. Want to get the whole thing to expand.
  handleRightClick(path) {
    // let props = ['$'];
    // let iterate = (obj, stack) => {
    //   for (var property in obj) {
    //     if (obj.hasOwnProperty(property)) {
    //       if (typeof obj[property] == "object") {
    //         iterate(obj[property], stack + '.' + property);
    //       } else {
    //         console.log(property + "   " + obj[property]);
    //         props.push(stack + '.' + property)
    //       }
    //     }
    //   }
    // }
    // iterate(this.props.data, '$');
    this.handleClick(path);
  }

  handleClick(path, cb = Function.prototype) {
    this.context.store.storeState = reducer(this.context.store.storeState, {
      type: 'TOGGLE_EXPAND',
      path: path,
    });
    this.setState(this.context.store.storeState, cb);
  }

  renderChildNodes(parentData, parentPath) {
    const { dataIterator } = this.props;
    const { depth } = this.props;

    const { onMatchFound, nodeRenderer, searchBoxText, filterStrings } = this.props;

    let childNodes = [];
    for (let { name, data, ...props } of dataIterator(parentData)) {
      const key = name;
      const path = `${parentPath}.${key}`;
      childNodes.push(
        <ConnectedTreeNode
          name={name}
          data={data}
          depth={depth + 1}
          path={path}
          key={key}
          onMatchFound={onMatchFound}
          dataIterator={dataIterator}
          nodeRenderer={nodeRenderer}
          searchBoxText={searchBoxText}
          filterStrings={filterStrings}
          {...props} // props for nodeRenderer
        />
      );
    }
    return childNodes;
  }

  render() {
    const { onMatchFound, data, dataIterator, path, depth } = this.props;

    const nodeHasChildNodes = hasChildNodes(data, dataIterator);
    const { expandedPaths } = this.state;
    const expanded = !!expandedPaths[path];
    const { nodeRenderer, searchBoxText, filterStrings } = this.props;

    return (
      <TreeNode
        onMatchFound={onMatchFound}
        expanded={expanded}
        onContextMenu={() => {
          this.handleRightClick(path);
        }}
        onClick={nodeHasChildNodes ? (e) => { this.handleClick(path) } : () => { }}
        // show arrow anyway even if not expanded and not rendering children
        shouldShowArrow={nodeHasChildNodes}
        // show placeholder only for non root nodes
        shouldShowPlaceholder={depth > 0}
        // Render a node from name and data (or possibly other props like isNonenumerable)
        nodeRenderer={nodeRenderer}
        searchBoxText={searchBoxText}
        filterStrings={filterStrings}
        {...this.props}
      >
        {// only render if the node is expanded
          expanded ? this.renderChildNodes(data, path) : undefined}
      </TreeNode>
    );
  }
}

ConnectedTreeNode.propTypes = {
  name: PropTypes.string,
  data: PropTypes.any,
  dataIterator: PropTypes.func,

  depth: PropTypes.number,
  expanded: PropTypes.bool,

  nodeRenderer: PropTypes.func,
};

ConnectedTreeNode.contextTypes = {
  store: PropTypes.any,
};

class TreeView extends React.PureComponent {
  static defaultProps = {
    expandLevel: 0,
    expandPaths: [],
  };

  constructor(props) {
    super(props);
    this.state = {
      expandLevel: 0
    }
    this.store = {
      storeState: {
        expandedPaths: getExpandedPaths(
          props.data,
          props.dataIterator,
          props.expandPaths,
          props.expandLevel,
        ),
      },
    };
  }

  componentWillReceiveProps(nextProps) {
    this.store = {
      storeState: {
        expandedPaths: getExpandedPaths(
          nextProps.data,
          nextProps.dataIterator,
          nextProps.expandPaths,
          nextProps.expandLevel,
          this.store.storeState.expandedPaths,
        ),
      },
    };
  }
  getChildContext() {
    return {
      store: this.store,
    };
  }

  static childContextTypes = {
    store: PropTypes.any,
  };

  render() {
    const { name, data, dataIterator } = this.props;
    const { nodeRenderer, filterStrings, searchBoxText } = this.props;
    const rootPath = DEFAULT_ROOT_PATH;
    return (
      <ConnectedTreeNode
        name={name}
        data={data}
        dataIterator={dataIterator}
        depth={0}
        path={rootPath}
        nodeRenderer={nodeRenderer}
        searchBoxText={searchBoxText}
        filterStrings={filterStrings}
      />
    );
  }
}

TreeView.propTypes = {
  name: PropTypes.string,
  data: PropTypes.any,
  dataIterator: PropTypes.func,

  nodeRenderer: PropTypes.func,
};

TreeView.defaultProps = {
  name: undefined,
};

export default TreeView;



