/**
 * Entry-file for the UI. Loads the store, the adapter, and the main UI file. Gets preferences, and renders the react component.
 */
import FSBLDependencyManager from "../../../common/dependencyManager";
import LoggerStore from "./stores/LoggerStore";
import LogAdapter from "./LogAdapter";
import React from "react";
import ReactDOM from "react-dom";
import App from "./app";
const myAdapter = new LogAdapter();
import ConfigClient from "../../../clients/configClient";
import { System } from "../../../common/system";

ConfigClient.initialize();
let saveLogToFileOnShutDown = false;

// convenience function for showing console if startup problem
window.showConsole = function () {
	let finWindow = System.Window.getCurrent();
	finWindow.focus(function () {
		finWindow.restore();
		finWindow.show();
		finWindow.updateOptions({
			options: {
				options: {
					alwaysOnTop: true
				}
			}
		}, () => {
			//Timeout so that on startup this sucker doesn't get buried by all the components coming up.
			setTimeout(() => {
				finWindow.updateOptions({
					options: {
						alwaysOnTop: false
					}
				});
			}, 3000);
		});
	});
};

function StartLoggerUI() {
	FSBLDependencyManager.startup.waitFor({
		services: ["preferencesService"],
	}, () => {
		ConfigClient.onReady(() => {
			ConfigClient.getValue({ field: "finsemble" }, function (err, value) {
				saveLogToFileOnShutDown = value.system.saveLogToFileOnShutDown;
			});
			ConfigClient.getPreferences((err, values) => {
				let showOnStartup = false, showOnError = false;
				try {
					showOnStartup = values["finsemble.preferences.loggerService.showOnStartup"];
					showOnError = values["finsemble.preferences.loggerService.showOnError"];
				} catch (e) {//bury the error
					console.log("Caught error getting preferences...", e);
				}
				//Set it, don't set the preference.
				LoggerStore.setShowOnStartup(showOnStartup, false);
				LoggerStore.setShowOnError(showOnError, false);
				if (showOnStartup) {
					window.showConsole();
				}
			});
		});
	});
	ReactDOM.render(
		<App />,
		document.getElementById("Logger-component-wrapper")
	);
}

StartLoggerUI();



