/**
 * All of the filters that can affect what's visible in the view.
 */
import React from "react";
import LoggerButton from "../../../../LoggerButton";
import LoggerStore from "../../../../../stores/LoggerStore";
import ResizeObserver from "resize-observer-polyfill";
import CategoryFilters from "./CategoryFilters";
import * as _debounce from "lodash.debounce";
const debouncedResize = _debounce(LoggerStore.emitResizeTimeout, 200);


// component for what is displayed locally in central logger
export default class LoggerClientControlList extends React.Component {
	constructor(props) {
		super(props);


		this.state = {
			highLight1: LoggerStore.getFilterStrings()[0],
			highLight2: LoggerStore.getFilterStrings()[1],
			highLight3: LoggerStore.getFilterStrings()[2],
			highLight4: LoggerStore.getFilterStrings()[3],
			devMode: LoggerStore.getDevModeState(),
			systemMode: LoggerStore.getSystemModeState(),
			perfMode: LoggerStore.getPerfModeState(),
			filter: {
				logic: LoggerStore.getFilterLogic()
			},
			clientListWidth: 2
		};

		this.highLight1 = this.highLight1.bind(this);
		this.highLight2 = this.highLight2.bind(this);
		this.highLight3 = this.highLight3.bind(this);
		this.highLight4 = this.highLight4.bind(this);
		this.getNewModeState = this.getNewModeState.bind(this);
		this.updateFilterLogic = this.updateFilterLogic.bind(this);
	}


	componentWillMount() {
		LoggerStore.addListener("ModeStateChange", this.getNewModeState);
		LoggerStore.addListener("filterLogicChange", this.updateFilterLogic);
	}

	componentWillUnmount() {
		LoggerStore.removeListener("ModeStateChange", this.getNewModeState);
		LoggerStore.removeListener("filterLogicChange", this.updateFilterLogic);
	}

	updateFilterLogic() {
		this.setState({
			filter: {
				logic: LoggerStore.getFilterLogic()
			}
		});
	}
	getNewModeState() {
		var newDevMode = LoggerStore.getDevModeState();
		var newSystemMode = LoggerStore.getSystemModeState();
		var newPerfMode = LoggerStore.getPerfModeState();
		this.setState({ devMode: newDevMode, systemMode: newSystemMode, perfMode: newPerfMode });
	}

	highLight1(event) {
		this.setState({ highLight1: event.target.value });
		LoggerStore.setFilter1(event.target.value);
	}

	highLight2(event) {
		this.setState({ highLight2: event.target.value });
		LoggerStore.setFilter2(event.target.value);
	}

	highLight3(event) {
		this.setState({ highLight3: event.target.value });
		LoggerStore.setFilter3(event.target.value);
	}

	highLight4(event) {
		this.setState({ highLight4: event.target.value });
		LoggerStore.setFilter4(event.target.value);
	}

	setFilterLogicAnd() {
		LoggerStore.setFilterLogic("AND");
	}

	setFilterLogicOr() {
		LoggerStore.setFilterLogic("OR");
	}

	componentDidMount() {
		const Observer = new ResizeObserver((entries, observer) => {
			setTimeout(debouncedResize, 200);
		});
		Observer.observe(document.getElementById("displayControl"));
	}
	render() {
		return (<div id="displayControl">
			<div className={"topDiv"}>
				<div className="logger-row">
					<CategoryFilters systemMode={this.state.systemMode} devMode={this.state.devMode} perfMode={this.state.perfMode} />
				</div>
			</div>
			<div className="logger-row">
				<div className="filter-logic ">
					<div className="fsbl-logger-button-group-label">
						Filter Logic
						</div>
					<div className="fsbl-logger-button-group fsbl-logger-buttons">
						<LoggerButton className={"fsbl-logger-toggle"}
							active={this.state.filter.logic === "AND"}
							onClick={this.setFilterLogicAnd}>AND</LoggerButton>
						<LoggerButton className={"fsbl-logger-toggle"}
							active={this.state.filter.logic === "OR"}
							onClick={this.setFilterLogicOr}>OR</LoggerButton>
					</div>
				</div>

			</div>
			<div className="logger-row">
				<div style={{ marginRight: "12px" }} className="">
					<input className="filter filter-0" type="text" placeholder="Filter 1" value={this.state.highLight1.str} onChange={this.highLight1} />
					<input className={"filter filter-1"} type="text" placeholder="Filter 2" value={this.state.highLight2.str} onChange={this.highLight2} />
					<input className={"filter filter-2"} type="text" placeholder="Filter 3" value={this.state.highLight3.str} onChange={this.highLight3} />
					<input className={"filter filter-3"} type="text" placeholder="Filter 4" value={this.state.highLight4.str} onChange={this.highLight4} />
				</div>
			</div>
		</div >);
	}
}


