/**
 * A button used all over the logger. can be a toggle, a regular old button, or a checkbox with state.
 */
import React from "react";

export default class LoggerButton extends React.PureComponent {
	constructor() {
		super();
	}
	render() {
		let classes = this.props.className || "";
		if (this.props.type === "checkbox") {
			classes += " inline-checkbox ";
		} else {
			classes += " fsbl-logger-button ";
		}
		if (this.props.active) {
			classes += this.props.activeClass || " active";
		}
		var Checkbox = () => {
			let classes = "checkbox";
			classes += this.props.active ? " active" : "";
			return (<div className={classes}>
				<div className="checkbox-background"></div>
			</div>);
		};

		return (<div className={classes}
			title={this.props.title || ""}
			onClick={this.props.onClick}>
			{this.props.type === "checkbox" &&
				<Checkbox />}
			{this.props.children}
		</div>);
	}
}


