/**
 * Bar on the left side of the logger that changes the active view.
 */
import React, { Component } from "react";
import LoggerStore from "../stores/LoggerStore";
class ViewSwitcher extends Component {
	getClasses(view) {
		let classes = "view-label";
		if (view === this.props.activeView) {
			classes += " view-label-active";
		}
		return classes;
	}
	render() {

		return (
			<div className="view-switcher">
				<div className={this.getClasses("logger")} onClick={LoggerStore.setLoggerVisible}>Logs</div>
				<div className={this.getClasses("config")} onClick={LoggerStore.setConfigVisible}>Conf</div>
			</div>
		);
	}
}

export default ViewSwitcher;


